import axios from 'axios'
import qs from 'qs'
// let baseUrl = '/aa'
let baseUrl = ''
axios.interceptors.request.use((req) => {
    if (req.url !== baseUrl + '/login') {
        req.headers.token = localStorage.getItem('token')
    }
    return req
})
axios.interceptors.response.use(res => {
    return res
})
export const reqLogin = (name, pass) => {
    //只携带了数据

    //如果携带的有文件
    /*let d=new FormData()
    d.append("name","zs");
    d.append("pass","123")
    d.append("img",file)
    return axios({
        url: baseUrl+"/login",
        method: "post",
        data: d
    })*/
    return axios({
        url: baseUrl + '/login',
        method: 'post',
        data: qs.stringify({
            name: name,
            pass: pass
        })
    })
}

//电影列表
export const reqMovieList = () => {
    return axios({
        url: baseUrl + "/movie",
        method: "get",

    })
}

//电影详情
export const reqMovieDetail = (id) => {
    return axios({
        url: baseUrl + "/getMovieDetail",
        method: "get",
        params: {
            id: id
        }
    })
}

//外卖
export const reqFood = () => {
        return axios({
            url: baseUrl + '/foodList',
            method: 'get'
        })
    }
    //外卖详情
export const reqFoodDetail = (id) => {
    return axios({
        url: baseUrl + "/getFoodDetail",
        method: "get",
        params: {
            foodId: id
        }
    })
}


