import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)
    // import login from '../pages/login/login.vue'
    // import index from '../pages/index/index.vue'
    // import movie from '../pages/movie/movie.vue'
    // import movieDetail from '../pages/movieDetail/movieDetail.vue'
    // import food from '../pages/food/food.vue'
    // import foodDetail from '../pages/foodDetail/foodDetail.vue'
    // import search from '../pages/search/search.vue'
    // import home from '../pages/home/home.vue'
    // import order from '../pages/order/order.vue'
    // import mine from '../pages/mine/mine.vue'
const movie = () =>
    import ('../pages/movie/movie.vue');
const movieDetail = () => Promise.resolve(
    import ('../pages/movieDetail/movieDetail.vue'));
const login = () =>
    import ('../pages/login/login.vue');
const index = () =>
    import ('../pages/index/index.vue');
const food = () =>
    import ('../pages/food/food.vue');
const foodDetail = () =>
    import ('../pages/foodDetail/foodDetail.vue')
const search = () =>
    import ('../pages/search/search.vue')
let router = new Router({
    mode: 'history',
    routes: [{
            path: '/login',
            name: '登录',
            component: login
        },
        {
            path: '/index',
            component: index,
            children: [{
                    path: 'home',
                    component: () =>
                        import ('../pages/home/home.vue'),
                    name: '首页'
                },
                {
                    path: 'order',
                    component: () =>
                        import ('../pages/order/order.vue'),
                    name: '订单'
                },
                {
                    path: 'mine',
                    component: () =>
                        import ('../pages/mine/mine.vue'),
                    name: '我的'
                },
                {
                    path: '',
                    redirect: 'home'
                }
            ]
        },
        {
            path: '/movie',
            name: '电影',
            component: movie,
            beforeEnter: (to, from, next) => {
                let type = localStorage.getItem('type');
                if (type === '1') {
                    next();
                } else {
                    next('/login')
                }
            }
        },
        {
            path: '/movieDetail',
            name: '电影详情',
            component: movieDetail,
            beforeEnter(to, from, next) {
                console.log("beforeEnter:路由独享守卫");
                let type = localStorage.getItem("type");
                if (type === "1") {
                    next();
                } else {
                    next("/login")
                }
            }
        },
        {
            path: '/food',
            name: '外卖',
            alias: '/waimai',
            component: food,
            beforeEnter: (to, from, next) => {
                if (localStorage.getItem('type') === '2') {
                    next();
                }
            }
        },
        {
            path: '/foodDetail/:foodId',
            component: foodDetail,
            name: '外卖详情',
            beforeEnter(to, from, next) {
                if (localStorage.getItem("type") === '2') {
                    next()
                }
            }
        },
        {
            path: '/search',
            component: search,
            name: '搜索'
        },
        {
            path: '*',
            redirect: '/login'
        },
    ],

})
router.beforeEach((to, from, next) => {
    if (to.path === '/login') {
        next();
        return
    }
    let isLogin = localStorage.getItem('isLogin');
    if (isLogin) {
        next();
        return;
    }
    next('/login')
})
export default router


