import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import { reqMovieList, reqMovieDetail } from '../utils/http'
const state = {
    movies: [],
    mDetail: {}
}
const mutations = {
        changeMovies(state, arr) {
            state.movies = arr;
        },
        changeMDetail(state, obj) {
            state.mDetail = obj;
        }
    }
    //处理逻辑
const actions = {
    reqMovie(context) {
        reqMovieList().then(res => {
            console.log(res);
            context.commit('changMovies', res.data.data)
        })
    },
    reqMovieDetail(context, id) {
        reqMovieList(id).then(res => {
            context.commit('changesMDetail', res.data.detail)
        })
    }
}
const getters = {
    movies(state) {
        return state.movies
    },
    mDetail(state) {
        return state.mDetail
    }
}

//导出仓库供main.js使用
export default new Vuex.Store({
    //state：仓库中的数据
    state: {},
    //mutations:专门用来修改state中的数据
    mutations: {},
    //actions:处理逻辑
    actions: {},
    //getters导出数据给组件，做state和组件之间的中间层
    getters: {}
})


